.post {
  background: rgb(252, 249, 248);  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #FFFFFF, rgb(252, 249, 248));  /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, left top, right top, from(#FFFFFF), to(rgb(252, 249, 248)));
	background: -webkit-linear-gradient(left, #FFFFFF, rgb(252, 249, 248));
	background: -o-linear-gradient(left, #FFFFFF, rgb(252, 249, 248));
	background: linear-gradient(to right, #FFFFFF, rgb(252, 249, 248)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border: 1px solid #bdced4;
  border-radius: 4px;
  margin: 20px;
  padding: 20px;
}

.post-date {
  color: #999;
  font-size: 12px;
  font-style: italic;
  margin-top: 2px;
}

.post-author {
  font-size: 14px;
  margin-top: 4px;
}

.post-family {
  border-color: #333;
  -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,.15);
          box-shadow: 0px 0px 6px 0px rgba(0,0,0,.15);
}


